}
.bg {
  background: #F6AF91;
  background: linear-gradient(150deg,#F5C9B1 15%,#F6AF91 70%,#F5C9B1 94%);
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-transform: skew(65deg, 0);
  -webkit-transform-origin: (top left);
  transform-origin: top left;
  transform: skew(65deg, 0);
